.loginsignup
{
  width:100%;
  height:90vh;
  background: #fce3fe;
  padding-top:30px ;
}
.loginsignup-container
{
    width:500px;
    height: 560px;
    background: white;
    margin: auto;
    padding: 8px 50px;

}

.loginsignup-container h1{
    margin: 20px 0px;
}
.loginsignup-fields
{
    display: flex;
    flex-direction: column;
    gap:25px;
    margin-top: 30px;   
}
.loginsignup-fields input{
    height:65px;
    width: 100%;
    padding-left: 20px;
    border:1px solid #c9c9c9;
    outline: none;
    color:#5c5c5c;
    font-size: 18px;
}

.loginsignup-container button{
    width:526px;
    height:72px;
    color:white;
    background: #ff4141;
    margin-top: 30px;
    border:none;
    font-size: 24px;
    font-weight: 500;
    cursor:pointer;
}
.loginsignup-login
{
    margin-top: 20px;
    color:#5c5c5c;
    font-size: 18px;
    font-weight: 500;
}
.loginsignup-login span{
    color:#ff4141;
    font-weight: 600;

}

.loginsignup-agree
{
    display: flex;
    align-items: center;
    margin-top: 25px;
    gap:20px;
    color:#5c5c5c;
    font-size: 18px;
    font-weight: 500;
}

@media(max-width : 1280px)
{
    .loginsignup
    {
        padding-top: 50px;
    }
    .loginsignup-container
    {
        width: auto;
        max-width: 500px;
        max-height: 500px;
    }
    .loginsignup-container h1 
    {
        margin: 10px 0px;
    }
    .loginsignup-fields
    {
        gap: 20px;
        margin-top: 20px;
    }
    .loginsignup-fields input 
    {
        height: 65px;
         width: 93%;
    }
    .loginsignup-container button 
    {
        width: 100%;
    }
    .loginsignup-login
    {
        font-size: 16px;
    }
    .loginsignup-agree 
    {
        font-size: 14px;
        gap: 10px;
    }
}
@media(max-width:800px)
{
    .loginsignup-container
    {
        padding: 20px 30px;
    }
    .loginsignup-container h1 
    {
        font-size: 24px;
    }
    .loginsignup-fields 
    {
        margin-top: 20px;
    }
    .loginsignup-fields input 
    {
        height: 50px;
    }
    .loginsignup-container button 
    {
        height: 50px;
        font-size: 16px;
    } 
}