.item
{
    width: 280px;
}
.item img{
    width:280px;
}
.item p{
    margin: 6px 0 px;
}
.item-prices
{
    display:flex;
    gap:20px;
}
.item-price-new
{
    color:#374151;
    font-size: 18px;;
    font-weight: 600;
}
.item-price-old
{
    color:#8c8c8c;
    font-size: 18px;
    font-weight: 500;
    text-decoration: line-through;
}
.item:hover{
    transition: 0.6s;
    transform: scale(1.05);
}

@media(max-width: 1280)
{
    .item
    {
        width: 220px;
        font-size: 14px;
    }
    .item img {
        width: 220px;
    }
    .item-price-old
    {
        font-size: 14px; 
    }
}

@media(max-width:1024px) {
    .item{
        width:120px;
        font-size: 13px ;
    }
    .item img 
    {
        font-size: 13px;
    }
    .item-price-old
    {
        font-size: 13px;
    }
}

@media(max-width:800px) {
    .item{
        width:120px;
        font-size: 12px ;
    }
    .item img 
    {
        font-size: 12px;
    }
    .item-price-old
    {
        font-size: 12px;
    }
}

@media(max-width: 500px)
{
    .item{
        width: 160px;
    }
    .item img 
    {
        width: 160px;
    }
}